$background-gradient: -webkit-linear-gradient(left, darkgreen, #d62229, #e97639, #792042, #33235b);
$header-height: 60px;

.header {
	height: $header-height;
	width: 100%;

	position: relative;

	background: lighten($color: darkslateblue, $amount: 40);

	&::before,
	&::after {
		content: "";
		position: absolute;
		width: 50px;
		height: 100%;
		top: 0;
		background: lighten($color: darkslateblue, $amount: 40);
	}

	&::before {
		left: -49px;
	}

	&::after {
		z-index: -1;
		right: -49px;
	}

	&__logo {
		font-size: 30px;
		height: 100%;
		line-height: $header-height;
		padding-left: 20px;

		font-weight: 500;
		text-transform: uppercase;

		&:hover {
			text-decoration: none;
			color: transparent;
		}

		display: inline-block;

		letter-spacing: 2px;

		background: $background-gradient;
		-webkit-background-clip: text;
		color: transparent;
		background-size: 3200px 200px;

		@keyframes animate-logo {
			0% {
				background-position: 0 3200px;
			}

			100% {
				background-position: 3200px 0;
			}
		}

		&::before {
			position: relative;
			content: "";

			top: -3px;

			width: 16px;
			height: 16px;

			margin-right: 10px;

			display: inline-block;

			background: $background-gradient;
			border-radius: 50%;
			background-size: 3200px 200px;
		}
	}

	&__logo,
	&::after,
	&__logo::before {
		animation: animate-logo 10s linear 0.5s infinite normal forwards;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		&::after {
			position: absolute;
			content: "";
			left: -49px;
			right: -49px;
			bottom: 0;

			background: $background-gradient;
			background-size: 3200px 200px;

			width: calc(100% + 100px -2px);
			height: 1px;
		}
	}

	&__search {
		max-width: 400px;
		width: 100%;

		top: 11px;
	}
}

.suggest {
	max-width: 400px;
	width: 100%;

	margin-right: 20px;
	margin-top: 11px;

	z-index: 1;

	&__content {
		margin-top: 11px;
	}

	&__link {
		&:hover {
			text-decoration: none;
			color: #0056b3;
		}
	}
}

@media screen and (max-width: 750px) {
	.suggest {
		width: 40%;
	}
}

@media screen and (max-width: 550px) {
	.suggest {
		display: none;
	}
}
